.Content {
    width: 100%;
    padding: 4rem 2rem;
}

*, ::after, ::before {
    box-sizing: border-box;
  }

.Content h1 {
    font-size: 2.5rem;
    margin-bottom: .5rem;
    font-weight: 500;
    line-height: 1.2;
    margin-top: 0;
    margin-bottom: 1rem;
}

.Nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;
}

.Nav li {
    display: inline-block;
    margin: 20px;
}

.Link {
    text-decoration: none;
    color: black;
}

.Nav a:hover,
.Nav a:active,
.Active {
    color: #007bff;
}

.Flags {
    margin: 5px;
    display: flex;
    height: 10px;
}

.Flags ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: left;
}

.Flags li {
    display: inline-block;
    margin: 2px;
    cursor: pointer;
}